
#app{
  width: 100%;
  /*overflow-x: hidden;*/
}

#total-show{
	width: 100%;
	overflow-x: hidden;
}
#bac{
	width: 100%;
	height: 0.8rem;
	border-radius: 0 0 19.6% 19.6%;
	background:-webkit-gradient(linear,left top, left bottom,from(rgb(6,181,131)),to(rgb(62,191,160)));
	background:linear-gradient(rgb(6,181,131),rgb(62,191,160));
}
#card{
	width: 90%;
	height: 2rem;
	margin-top: -0.58rem;
	margin-left: 5.2%;
	background:rgb(255,255,255);
	border-radius: 0.1rem;
	-webkit-box-shadow: 0rem 0.035rem 0.135rem 0.02rem rgba(181,181,181,0.16);
	box-shadow: 0rem 0.035rem 0.135rem 0.02rem rgba(181,181,181,0.16);
	z-index: 2;
}
.bg{
	width:48%;
	height: 1.57rem;
	margin-top: 0.22rem;
	margin-left: 47%;
	z-index:4;
}
#card div{
	width: 1.28rem;
	height: 0.19rem;
	z-index:6; 
	margin-top:-1.75rem;
	margin-left: 0.25rem;
}
.user{
	font-size: 0.2rem;
	font-weight: bold;
	float: left;
	margin-top: -0.05rem;
}
.info{
	width: 0.5rem;
	height: 0.15rem;
	line-height: 0.16rem;
	background-color: rgb(127,160,248);
	float: left;
	margin-top: 0.03rem;
	margin-left: 0.12rem;
	border-radius: 0.075rem;
	font-size: 0.1rem;
	color: #fff;
}
.info img{
	width: 0.1rem;
	height: 0.1rem;
	float: left;
	margin-top: 0.025rem;
	margin-left: 0.055rem;
}
.info span{
	margin-left: 0.05rem;
}
#card .detail{
	width: 2.158rem;
	height: 0.97rem;
	margin-top: 0.26rem;
	margin-left: 0.25rem;
}
ul{
	list-style-type: none;
	margin: 0rem;
	padding: 0rem;
}
a{

	text-decoration: none;
}
.detail ul li{
	width: 2.158rem;
	height: 0.18rem;
	line-height: 0.16rem;
	font-size: 0.14rem;
	margin-top: 0.16rem;
}
.detail img{
	width: 0.15rem;
	height: 0.13rem;
}
.detail .key{

	margin-left: 0.19rem;
}
.detail .val{
	margin-left: 0.645rem;
	font-size: 0.15rem;
	color: rgb(137,137,137);
}
.detail .val-1{
	margin-left: 0.35rem;
	font-size: 0.15rem;
	color: rgb(137,137,137);
}
.personal-doc,.personal-his,.family-his,.personal-life,.personal-hab{
	width: 90%;
	height: 0.65rem;
	margin-top: 0.15rem;
	margin-left: 5%;
	border-radius: 0.1rem;
	-webkit-box-shadow: 0rem 0.035rem 0.135rem 0.02rem rgba(181,181,181,0.16);
	box-shadow: 0rem 0.035rem 0.135rem 0.02rem rgba(181,181,181,0.16);
	background-color: rgb(255,255,255);
}
.no{
	width: 100%;
	height: 0.1rem;
}
.personal-doc ul li,.personal-his ul li,.family-his ul li,.personal-life ul li,.personal-hab ul li{
	width: 3.45rem;
	height: 0.65rem;
	line-height: 0.65rem;
	font-size: 0.17rem;
	color: rgb(109,109,109);
}
.personal-doc .icon1,.personal-his .icon1,.family-his .icon1,.personal-life .icon1,.personal-hab .icon1{
	width: 13%;
	height: 0.45rem;
	margin-top: 0.1rem;
	margin-left: 6%;
	float: left;
}
.personal-doc .line,.personal-his .line,.family-his .line,.personal-life .line,.personal-hab .line{
	display: block;
	width: 0.5%;
	height: 0.35rem;
	margin-left: 13%;
	margin-top: 0.15rem;
	background:rgb(238,238,238);
	float: left;
}
.personal-doc .word,.personal-his .word,.family-his .word,.personal-life .word,.personal-hab .word{

	margin-left: 9.9%;
}
.personal-doc .icon2,.personal-his .icon2,.family-his .icon2,.personal-life .icon2,.personal-hab .icon2{
	width: 2.5%;
	height: 0.14rem;
}
.personal-doc .icon2,.personal-life .icon2{
	margin-left: 0.6rem;
}
.personal-his .icon2{
    margin-left: 0.77rem;
}
.family-his .icon2{
	margin-left: 1.12rem;
}
.personal-hab .icon2{
	margin-left: 0.94rem;
}
#none{
	width: 66.67%;
	height:2.5rem;
	display: block;
	margin: 50% auto;
}


#personal-doc .head{
	width: 100%;
}
#personal-doc .head{
	height: 0.6rem; 
	margin-top: 0.005rem;
}
#personal-doc .content{
	margin-top: 0.1rem;
}
#personal-doc .head span,#personal-doc .head label{
	line-height: 0.35rem;
}
#personal-doc .head span img{
	position: absolute;
	width: 0.35rem;
	height: 0.35rem;
	padding-top: 0.125rem;
	padding-left: 0.2rem;
}
#personal-doc .head label{
	display: inline-block;
	position: absolute;
	font-size: 0.17rem;
	padding-top: 0.125rem;
	padding-left: 0.75rem;
	color: rgb(48,48,48);
}
ul{
	list-style-type: none;
	margin:0rem;
	padding: 0rem;
}
.content ul li{
	width: 100%;
	height: 0.55rem;
	line-height: 0.55rem;
	font-size: 0.15rem;
	border-bottom: 1px solid #f0f0f0;
}
.content ul li span.key{
	float: left;
	margin-left: 0.19rem;
}
.content ul li span.val{
	float: right;
	margin-right: 0.1rem;
}
	


